import React from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import { Link, useLocation, useParams } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import {
  merchantView,
  updateStatusMerchant,
} from "../../apiServices/home/homeHttpService";
import { showAlert } from "../commonComponent/alertManager";
import { useAlert } from "react-alert";

function MerchantDetails() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  let { id } = useParams();
  const alert = useAlert();

  const { data: details, refetch } = useQuery({
    queryKey: ["merchantDetail", id],
    queryFn: async () => {
      return merchantView(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.merchant,
  });

  const changeStatus = async (v) => {
    const formData = {
      status: v,
    };
    const response = await updateStatusMerchant(id, formData);
    if (!response.error) {
      showAlert(alert, response.message, { timeout: 3000 });
      refetch();
    } else {
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  console.log(details);

  return (
    <>
      <Header />
      <div className="admin_main">
        <SideBar />

        <div className="admin_contentpart">
          <Link
            to={`/admin/merchant-management?tab=${
              queryParams.get("tab") || 1
            }&page=${queryParams.get("page") || 1}`}
            className="comman_btn w-fit"
          >
            <i className="fa fa-arrow-left" />
          </Link>

          <div className="row">
            <div
              className={
                details?.restaurant?.length
                  ? "col-lg-4 col-md-12 col-12"
                  : " col-12"
              }
            >
              <div className="row comman_design mx-0 mt-4">
                <div className="col-12">
                  <div className="row comman_table pt-md-4 pb-md-4 pb-3 px-md-4 px-2">
                    <div className="col-12">
                      <div>
                        <div className="d-flex justify-content-center">
                          <div className="user-img-wrapper">
                            <img
                              src={
                                details?.profile
                                  ? details?.profile
                                  : "../../assets/img/user1.png"
                              }
                              alt=""
                              className="w-100 h-100"
                            />
                          </div>
                        </div>
                      </div>
                      <div>
                        <div className="mt-4 user-view-wrapper">
                          <div className="mt-2">
                            <strong>Name</strong>
                            <span>{details?.name}</span>
                          </div>
                          <div className="mt-2">
                            <strong>Email</strong>
                            <span style={{ wordBreak: "break-word" }}>
                              {details?.email}
                            </span>
                          </div>
                          <div className="mt-2">
                            <strong>Mobile No.</strong>
                            <span>{details?.mobileNumber}</span>
                          </div>
                          <div className="mt-2">
                            <strong>Status</strong>

                            <span
                              className={
                                details?.status
                                  ? "status_accepted"
                                  : "status_declained"
                              }
                            >
                              {details?.status ? "Active" : "Inactive"}
                            </span>
                          </div>
                          <div className="mt-2">
                            <strong>Rating</strong>
                            <span className="ps-1">
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star main-color leading-5" />
                              <i className="fa fa-star leading-5" />
                            </span>
                          </div>
                          <div className="mt-2">
                            <strong>Review</strong>
                            <span>Good service</span>
                          </div>
                          <div className="d-flex mt-2">
                            <strong>Status Update </strong>
                            <span>
                              <div className="ms-2 form-check form-switch custom-switch">
                                <input
                                  className="form-check-input "
                                  type="checkbox"
                                  onChange={(e) =>
                                    changeStatus(e.target.checked)
                                  }
                                  checked={details?.status}
                                />
                                <label
                                  className="form-check-label"
                                  htmlFor="flexSwitchCheckChecked"
                                />
                              </div>
                            </span>
                          </div>
                          <div className="mt-2">
                            <strong>Description</strong>
                            <span>
                              Lorem ipsum dolor sit amet consectetur adipisicing
                              elit.
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {details?.restaurant?.length ? (
              <div className="col-lg-8 col-md-12 col-12 mt-4 mt-md-0">
                <div className="row comman_design mx-0 mt-4">
                  <div className="col-12">
                    <div className="row comman_table pt-md-4 pb-md-4 pb-3 px-md-4 px-2">
                      {details?.restaurant?.map((item, index) => (
                        <div className="col-12" key={index}>
                          <div className="row">
                            <div className="col-md-12">
                              <div className="d-flex">
                                <div className="w-100 rounded-3 overflow-hidden merchant-img-wrapper">
                                  <img
                                    src={item?.restaurantPic[0]}
                                    alt=""
                                    className="w-100 h-100"
                                  />
                                </div>
                              </div>
                            </div>
                            <div className="col-md-12 position-relative py-2 w-75">
                              <h4 className="fs-4 fw-medium mb-0">
                                {item?.name}
                              </h4>
                              <div className="d-flex gap-1 mt-2">
                                <div>
                                  <i className="fas fa-map-marker-alt" />
                                </div>
                                <p className="fs-6 fw-light">
                                  {item?.location?.address}
                                </p>
                              </div>
                            </div>
                          </div>
                          <div>
                            <div className="d-flex flex-wrap justify-content-between">
                              <div className="mb-4 custom-width-merchants-view card py-2 px-3 rounded-3 bg-main text-white">
                                <h6 className="fw-semibold fs-6">
                                  Restaurant Features
                                </h6>
                                <ul className="Cuisines-list p-0 mt-3">
                                  {item?.features?.map((item2, index2) => (
                                    <li
                                      className="px-3 leading-2 rounded-3 cursor-pointer"
                                      key={index2}
                                    >
                                      {item2?.name}
                                    </li>
                                  ))}
                                </ul>
                              </div>
                              <div className="mb-4 custom-width-merchants-view card py-2 px-3 rounded-3 bg-custom-secondary text-white">
                                <h6 className="fw-semibold fs-6">
                                  Restaurant Cuisines
                                </h6>
                                <ul className="p-0 Cuisines-list mt-3">
                                  {item?.cuisines?.map((item2, index2) => (
                                    <li
                                      className="px-3 leading-2 rounded-3 cursor-pointer"
                                      key={index2}
                                    >
                                      {item2?.name}
                                    </li>
                                  ))}
                                </ul>
                              </div>
                            </div>
                            <p className="fw-light fs-6">{item?.about}</p>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            ) : (
              ""
            )}
            {/* <div className="col-12 mt-4">
              <div className="row">
                <div className="col-md-4 col-12 mt-4 mt-md-0">
                  <div className="card py-4 px-4 rounded-3 shadow">
                    <video
                      src="https://codingyaar.com/wp-content/uploads/video-in-bootstrap-card.mp4"
                      className="card-img-top"
                      controls
                    />
                    <div className="card-body px-0 pt-4 pb-0">
                      <h5 className="card-title">Spicy Pasta</h5>
                      <p className="card-text">
                        Lorem ipsum dolor, sit amet consectetur adipisicing
                        elit. Explicabo vel odio exercitationem ut enim minima
                        facere excepturi nihil quia quisquam possimus ducimus
                        maxime iste fugit dicta harum, debitis dignissimos
                        placeat?
                      </p>
                    </div>
                  </div>
                </div>
                <div className="col-md-4 col-12 mt-4 mt-md-0">
                  <div className="card py-4 px-4 rounded-3 shadow">
                    <video
                      src="https://codingyaar.com/wp-content/uploads/video-in-bootstrap-card.mp4"
                      className="card-img-top"
                      controls
                    />
                    <div className="card-body px-0 pt-4 pb-0">
                      <h5 className="card-title">Spicy Pasta</h5>
                      <p className="card-text">
                        Lorem ipsum dolor, sit amet consectetur adipisicing
                        elit. Explicabo vel odio exercitationem ut enim minima
                        facere excepturi nihil quia quisquam possimus ducimus
                        maxime iste fugit dicta harum, debitis dignissimos
                        placeat?
                      </p>
                    </div>
                  </div>
                </div>
                <div className="col-md-4 col-12 mt-4 mt-md-0">
                  <div className="card py-4 px-4 rounded-3 shadow">
                    <video
                      src="https://codingyaar.com/wp-content/uploads/video-in-bootstrap-card.mp4"
                      className="card-img-top"
                      controls
                    />
                    <div className="card-body px-0 pt-4 pb-0">
                      <h5 className="card-title">Spicy Pasta</h5>
                      <p className="card-text">
                        Lorem ipsum dolor, sit amet consectetur adipisicing
                        elit. Explicabo vel odio exercitationem ut enim minima
                        facere excepturi nihil quia quisquam possimus ducimus
                        maxime iste fugit dicta harum, debitis dignissimos
                        placeat?
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div> */}
          </div>
        </div>
      </div>
    </>
  );
}

export default MerchantDetails;
